import React from 'react';
import PropTypes from 'prop-types';
import { platform, IOS } from '@vkontakte/vkui';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import PanelHeaderButton from '@vkontakte/vkui/dist/components/PanelHeaderButton/PanelHeaderButton';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import './RecordMenuDayStyle.css';

const osName = platform();

const day = new Date();

const Month = [ 
    "Январь","Февраль", "Март",
    "Апрель", "Май", "Июнь", "Июль",
    "Август","Сентябрь", "Октябрь",
    "Ноябрь","Декабрь"
]
 
const MonthDay = new Map([
        ["Январь", 31],
        ["Февраль", 28],
        ["Март", 31],
        ["Апрель", 30],
        ["Май", 31],
        ["Июнь", 30],
        ["Июль", 31],
        ["Август", 31],
        ["Сентябрь", 30],
        ["Октябрь", 31],
        ["Ноябрь", 30],
        ["Декабрь", 31]
    ]
)

function GetCurrentMonth(){
    var index = day.getMonth();

    return Month[Number(index)];
}

const RecordMenuDay = props => (
	<Panel id={props.id}>
		<PanelHeader
			left={<PanelHeaderButton onClick={props.go} data-to="mainMenu">
				{osName === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			</PanelHeaderButton>}>Laundry</PanelHeader>
		<Div className="MainRecordDay">
			<Div className="TextChooseDay">
				Выберите день 
			</Div>
            <Row/>
		</Div>
	</Panel>
);

function NameMonth(props){
    var day = GetCurrentMonth();
    console.log(day);
    return(
        <Div className = "TextMonth">
            {day}
        </Div>
    )
}

class CalendarBlock extends React.Component {
	constructor(props) {
	  super(props);
	  this.state = {
		};
    }
    render() {
		return (
			<Div className= "CalendarMain">
                <NameMonth/>

			</Div>
		);
	}
}


function Row(props){
    return(
        <Div className = "RowButton">
            <ButtonDay className = "ButtonDay"/>
            <ButtonDay className = "ButtonDay"/>
            <ButtonDay className = "ButtonDay"/>
            <ButtonDay className = "ButtonDay"/>
            <ButtonDay className = "ButtonDay"/>
            <ButtonDay className = "ButtonDay"/>
            <ButtonDay className = "LastButtonDay"/>
        </Div>
    )
}

function ButtonDay(props){
    return(
        <Div className = "ButtonDay">
            <Button size="l" mode="outline">
                ПН
            </Button>
        </Div>
    )
}


class Calendar extends React.Component {
	constructor(props) {
	  super(props);
	  this.state = {
		};
    }
    render() {
		return (
			<Div className= "Calendar">
                <table id = "Table">
                    <Row />
                    <Row />
                    <Row />
                    <Row />
                    <Row />
                    <Row />
                    <Row />
                </table>
			</Div>
		);
	}
}

RecordMenuDay.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default RecordMenuDay;
